
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-out .5s;
}
.white{
    margin-right:10px;
    padding: .5%
}
li{
    padding-left: 5%;
    padding-right: 5%
}
@keyframes bounce-in {
0% {
        transform: scale(0);
}
50% {
        transform: scale(1.5);
}
100% {
        transform: scale(1);
}
}
@keyframes bounce-out {
0% {
        transform: scale(1);
}
50% {
        transform: scale(1.5);
}
100% {
        transform: scale(0);
}
}



